



































































.auth-form {
  width: 480px;
  max-width: 100%;
  background: #fff;
  margin: 0 auto;
  padding: 15px;
  margin-top: 150px;
  border-radius: 7px;
  box-shadow: 0 5px 20px 0px rgba(0, 0, 0, 0.1);
}
.auth-form h1 {
  font-weight: 300;
}
.auth-form > div {
  margin-top: 15px;
}
.auth-form input {
  height: 32px;
  background: #e4e4e4;
  border: none;
  border-radius: 3px;
  margin-bottom: 20px !important;
  font-size: 14px !important;
  padding-left: 10px;
}
.auth-form input:focus {
  border-bottom-color: #555;
}
.auth-form label,
.auth-form input {
  display: block;
  width: 100%;
  margin-bottom: 5px;
  font-size: 18px;
}
.auth-form button {
  font-size: 18px;
  background: #fff;
  border: 1px solid #41b883;
  padding: 4px 6px;
  margin: 0;
  border-radius: 4px;
  cursor: pointer;
  color: black;

  height: 40px;
  font-size: 20px;
}

.auth-form .btn-group {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.auth-form .btn-group button {
  width: 48%;
}

.auth-form button.disabled {
  background: #ccc;
  color: #999;
  cursor: default;
}

.auth-form .signup-submit {
  width: 100%;
}
.auth-form hr {
  margin-top: 20px;
}
.auth-form .social-providers {
  text-align: center;
}
.auth-form .social-providers a {
  color: #41b883;
  font-size: 36px;
  padding: 4px;
}



