  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
html {
  position: relative;
  min-height: 100%;
}
body {
  font-family: "Open Sans", sans-serif;
  min-height: 100%;
}
body > div {
  /* padding: 50px 16px; */
  /* padding-top: 50px; */
  padding-top: 100px;
  padding-bottom: 30px;
  min-height: 100vh;
  background: #eee;
  position: relative;
  /* margin-bottom: 50px; */
  z-index: 1;
  /* box-shadow: 0 2px 5px #111; */
}

footer {
  box-shadow: 0 0 10px 1px rgba(161, 161, 161, 0.37);
  background: white;
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 30px;
  padding: 0 15px;
  display: -ms-flexbox;
  display: flex;

  -ms-flex-align: center;

      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

footer span {
  font-size: 15px;
}

.clearfix:after {
  content: "";
  display: table;
  clear: both;
}



