


































.note {
  background: #fff;
  border-radius: 7px;
  box-shadow: 0 5px 20px 0px rgba(0, 0, 0, 0.1);
  padding: 20px;
  padding-bottom: 10px;
  transition: box-shadow 0.2s, transform 0.2s;
  cursor: default;
  margin: 10px;
  width: calc(25% - 20px);

  display: -ms-flexbox;

  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}
.note.medium {
  width: calc(50% - 20px);
}
.note-title {
  font-size: 1.2rem;
  margin-bottom: 5px;
  word-wrap: break-word;
}

.note-email {
  font-size: 0.85rem;
  display: block;
  color: gray;
}

.note-date {
  font-size: 0.85rem;
  display: block;
  margin-bottom: 10px;
  color: gray;
}

.note-content {
  font-size: 1rem;
  margin-bottom: 25px;
  white-space: pre-wrap;
  word-wrap: break-word;
  font-family: inherit;
}

.note-buttons {
  margin-top: auto;
}

.note button {
  color: #777;
  border: none;
  font-size: 20px;
  cursor: pointer;
  transition: background-color 0.3s;
  margin: 0 4px 0 0;
  float: right;
  margin-left: 5px;
  padding: 5px 10px;
  transform: translateX(15px);
  background: transparent;
}

.note:hover,
.note:focus {
  box-shadow: 0 7px 10px 0px rgba(0, 0, 0, 0.1);
  transform: translateY(-5px);
  cursor: pointer;
}
.note:hover button,
.note:focus button {
  /* opacity: 0.6; */
}

.note button.edit {
}

.note button.edit:hover {
  color: #66bb6a;
}

.note button.delete:hover {
  /* background: #d32f2f; */
  /* color: white; */
  color: #d32f2f;
}



